@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.lien-he .container {
  padding-top: 50px;
  padding-bottom: 70px;
  background: #ffffff;
}
.lien-he .title {
  text-align: center;
  margin-bottom: 30px;
}
.lien-he .title h1 {
  font-size: 35px;
  color: #333;
  text-transform: uppercase;
}
.lien-he .title p {
  color: #534b40;
  font-family: Muli, sans-serif;
  font-weight: 300;
  font-size: 16px;
}
.lien-he .info {
  background: #f8f8f8;
  padding: 30px;
}
@media (max-width: 992px) {
  .lien-he .info {
    margin-bottom: 30px;
  }
}
@media (max-width: 576px) {
  .lien-he .info {
    padding: 20px;
  }
}
.lien-he .info p {
  position: relative;
  padding-left: 25px;
}
.lien-he .info p:before {
  position: absolute;
  top: 5px;
  left: 0;
}
.lien-he .info figure figcaption {
  margin: 15px 0;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  color: #f26f21;
}
.lien-he .info b, .lien-he .info span, .lien-he .info strong {
  font-family: Muli, sans-serif;
  font-size: 16px;
  color: #666666;
}
.lien-he .info b {
  color: #f26f21;
}
.lien-he .info span {
  font-weight: 300;
}
.lien-he .info .lnr {
  font-size: 18px;
}
.lien-he .form-group {
  margin-bottom: 15px;
}
.lien-he .form-group label {
  display: block;
  font-family: Muli, sans-serif;
  margin-bottom: 5px;
}
.lien-he .form-group input,
.lien-he .form-group select,
.lien-he .form-group textarea {
  width: 100%;
  font-family: Muli, sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #f26f21;
  padding: 10px 20px;
  border: 1px solid #E1E1E1;
  border-radius: 3px;
  outline: none;
  height: 41.2px;
}
.lien-he .form-group input:focus,
.lien-he .form-group select:focus,
.lien-he .form-group textarea:focus {
  box-shadow: 0 0 5px 2px #e1e1e1;
}
.lien-he .form-group textarea {
  height: 150px;
}
.lien-he .form-group select {
  color: #999;
}
.lien-he .form-group select option[value=""][disabled] {
  display: none;
  color: #999;
}
.lien-he .wrap-form {
  position: relative;
}
.lien-he .frm-captcha {
  display: flex;
  align-items: center;
  flex: 0 0 64%;
  max-width: 64%;
}
@media (max-width: 992px) {
  .lien-he .frm-captcha {
    flex-direction: column;
    align-items: flex-start;
  }
}
.lien-he .frm-captcha .frm-captcha-input {
  order: 1;
}
@media (max-width: 992px) {
  .lien-he .frm-captcha .frm-captcha-input {
    margin-top: 10px;
  }
}
.lien-he .frm-captcha .frm-captcha-input .label {
  display: none;
}
.lien-he .frm-captcha .frm-captcha-input input {
  width: 250px;
  padding-left: 20px;
}
@media (min-width: 576px) and (max-width: 1200px) {
  .lien-he .frm-captcha .frm-captcha-input input {
    width: 150px;
  }
}
@media (max-width: 576px) {
  .lien-he .frm-captcha .frm-captcha-input input {
    width: 180px;
  }
}
.lien-he .frm-captcha .RadCaptcha_Default {
  width: auto !important;
  position: relative;
  margin-right: 20px;
}
@media (max-width: 991.98px) {
  .lien-he .frm-captcha .RadCaptcha_Default {
    margin-right: 0;
    margin-top: 10px;
  }
}
.lien-he .frm-captcha .RadCaptcha_Default span {
  position: absolute;
  color: red;
  font-family: Muli, sans-serif;
  font-weight: bold;
  bottom: -27px;
  left: 0;
}
.lien-he .frm-captcha .RadCaptcha_Default .rcRefreshImage {
  font-size: 0;
  text-decoration: none;
  color: #999999;
  display: block;
  margin-left: 20px;
}
.lien-he .frm-captcha .RadCaptcha_Default .rcRefreshImage:after {
  content: "\f01e";
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  font-size: 2rem;
  color: #999999;
}
.lien-he .frm-captcha .RadCaptcha_Default > div > div {
  display: flex;
  align-items: center;
}
.lien-he .frm-captcha .RadCaptcha_Default > div > div img {
  width: 132px !important;
}
.lien-he .frm-btnwrap label {
  display: none;
}
@media (min-width: 992px) {
  .lien-he .frm-btnwrap {
    position: absolute;
    bottom: 4px;
    right: 0;
    margin: 0;
    width: 30%;
  }
}
@media (max-width: 768px) {
  .lien-he .frm-btnwrap {
    text-align: center;
  }
}
.lien-he .frm-btn-submit {
  font-family: Muli, sans-serif;
  font-weight: 600;
  font-size: 14px;
  background: #f26f21;
  color: #fff !important;
  padding: 10px 50px;
  border: 1px solid #E1E1E1;
  border-radius: 3px;
  text-transform: uppercase;
  outline: none;
}
@media (max-width: 768px) {
  .lien-he .frm-btn-submit {
    width: 150px !important;
    margin: 0 auto;
  }
}
.lien-he .maps {
  width: 100%;
  height: 500px;
  margin-top: 50px;
}
.lien-he .maps iframe {
  width: 100%;
  height: 100%;
}
.lien-he .frm-btn-reset {
  display: none;
}